import React from 'react'

processStr = (arr) => {
  let str = ""
  arr.forEach(el => {
    str = str + "|" + el
  })
  return str.substr(1)
}

class Cart extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      id: props.id,
      name: props.name,
      price: props.price,
      desc: props.desc,
      flavors: props.flavors,
      url: "https://localhost:3000/snipcartParser"
    }
  }

  render() {
    let flavOptions = processStr(this.state.flavors)
    return (
      <button 
          className="snipcart-add-item BuyButton"
          data-item-id={this.state.id}
          data-item-name={this.state.name}
          data-item-url={this.state.url}
          data-item-description={this.state.desc}
          data-item-price={this.state.price}
          data-item-custom1-name="flavors"
          data-item-custom1-required="true"
          // data-item-custom1-options={}
        >
        ADD TO CART ({this.state.price}) {flavOptions}
      </button>
    )
  }
}

export default (Cart)